import React from 'react';
import { BulletList } from 'react-content-loader';

const inlinks = ({ data }) => {
  return (
    <>
      <h2>  </h2>
      {data ? (
        <div className='list'>
          <ul>
            {data.inlinks.map((e, i) => (
              <li key={i}>
                <a href={e.href}>
                  {e.text && !e.text.trim()
                    ? '  '
                    : e.text === ''
                    ? '  '
                    : e.text}
                </a>
              </li>
            ))}
          </ul>
        </div>
      ) : (
        <>
          <BulletList
            style={{ display: 'block', margin: 'auto', marginTop: '40px' }}
            width={'80%'}
          />
        </>
      )}
    </>
  );
};

export default inlinks;
